import { Box, Card, List, ListItemButton, ListItemIcon, ListItemText } from '@mui/material'
import React from 'react'

const PathDashboard = () => {
  return (
    <Box p={2}> 
        <Card style={{borderRadius: '30px 30px 0 0' , paddingTop: '20px' , height:'calc(100% - 232px)' ,
    margin:'0px 12px'
    }}>
          <a href='#' >
            <List>
                <ListItemButton>
                    <ListItemIcon>
                        
                    </ListItemIcon>
                    <ListItemText primary="" />
                </ListItemButton>
            </List>

          </a>
        </Card>
    </Box>
  )
}

export default PathDashboard